.header {

  &__top {
    width: 100%;
    padding: 12px 13px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;

    @include lg {
      justify-content: space-around;
    }
    @include md {
      flex-wrap: wrap;

      &::before{
        display: none;
      }
      &::after{
        display: none;
      }
    }

    &::before {
      content: '';
      width: 550px;
      height: 1px;
      top: 20px;
      left: 0;
      position: absolute;
      background-color: #2f2c34;

      @include lg {
        width: 400px;
      }
    }

    &::after {
      content: '';
      width: 550px;
      height: 1px;
      top: 20px;
      right: 0;
      position: absolute;
      background-color: #2f2c34;

      @include lg {
        width: 400px;
      }
    }

    &-left {
      display: flex;
      height: 100%;
      font-weight: 600;
      margin-top: 25px;
      flex-direction: row;


      @include lg {
        flex-direction: column;
      }

      @include sm {
        display: none;
      }

    }

    &-logo {
      width: 215px;
      margin: 0;
      height: 100%;
      align-items: center;

      img {
        margin-left: -16px;
      }
    }

    &-rigth {
      margin-top: 25px;

      display: flex;
      flex-direction: row;
      justify-content: space-between;
      height: 100%;

      @include lg {
        flex-direction: column;
        gap: 10px;

      }

      @include md {
        width: 100%;
        gap: 0;
        flex-direction: row;

      }
    }
  }

  &__nav {
    font-family: 'romul', sans-serif;
  }

  &-phone {
    padding: 0;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    display: flex;
    flex-wrap: nowrap;
    margin-right: 60px;
    line-height: 1.4;
    flex-direction: column;
    align-items: flex-start;
    @include md {
      font-size: 14px;
      margin-bottom: 10px;
    }
    span {
      text-transform: uppercase;
      color: #d0aa8c;
      font-size: 11px;
      position: relative;
      @include md {
        font-size: 9px;

      }
      &::before {
        content: '';
        position: absolute;
        width: 40px;
        height: 1px;
        top: 50%;
        right: -50px;
        background-color: #d0aa8c;
      }

    }
  }

  .nav-bar {
    width: 100%;
    background-color: #2f2c34;
  }

  .btn {
    font-weight: 500;
    text-transform: uppercase;
    font-size: 13px;
    padding: 8px 35px;
    border: 1px solid black;
    margin-left: 25px;
    white-space: nowrap;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    @include md {
      margin-left: 0;
    }
    &:hover {
      border-color: #d0aa8c;
      background-color: #d0aa8c;
      color: #fff;
    }
  }

  &__nav {
    position: relative;
    margin: 0 15px;
    padding: 21px 15px;
    font-size: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #2f2c34;

    @include md {
      display: none;
      flex-direction: column;
      justify-content: space-evenly;
      width: 50%;
      position: fixed;
      top: 0;
      height: 100vh;
      z-index: 900;
      padding-left: 40px ;
    }


    :first-child {
      margin-left: 0;
    }

    :last-child {
      padding: 0;
      border-right: none;
    }

    &-item {
      color: white;
      border-right: 1px solid #fff;
      padding: 0 70px 0 0;

      @include lg {
        padding-right: 30px;
      }

      @include md {
        border: none;
      }
      a {
        width: 100%;
        transition: all .3s ease;
        color: #fff;
        position: relative;

        &:hover {
          color: #d0aa8c;

        }

      }

    }
  }

  .menu_burger {
    width: 100px;
    height: 100%;
    display: none;
    color: white;
    font-size: 38px;
    z-index: 100;
text-align: right;
padding: 0 20px;
    @include md {
      display: block;
      margin: 0 20px 0 auto;
    }
  }

  .d-block{
    display: flex;
  }
}
