@import url(_iconsfont.css);
:root {
  --color-black: #000;
  --color-white: #fff;
}

@font-face {
  font-family: "ico";
  src: url("../fonts/ico.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "romul";
  src: url("../fonts/romul.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

