html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "asd", sans-serif;
  background-color: var(--color-white);
  font-family: 'Montserrat', sans-serif;
  color: #2f2c34;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header__top {
  width: 100%;
  padding: 12px 13px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

@media (max-width: 1190px) {
  .header__top {
    justify-content: space-around;
  }
}

@media (max-width: 768px) {
  .header__top {
    flex-wrap: wrap;
  }
  .header__top::before {
    display: none;
  }
  .header__top::after {
    display: none;
  }
}

.header__top::before {
  content: '';
  width: 550px;
  height: 1px;
  top: 20px;
  left: 0;
  position: absolute;
  background-color: #2f2c34;
}

@media (max-width: 1190px) {
  .header__top::before {
    width: 400px;
  }
}

.header__top::after {
  content: '';
  width: 550px;
  height: 1px;
  top: 20px;
  right: 0;
  position: absolute;
  background-color: #2f2c34;
}

@media (max-width: 1190px) {
  .header__top::after {
    width: 400px;
  }
}

.header__top-left {
  display: flex;
  height: 100%;
  font-weight: 600;
  margin-top: 25px;
  flex-direction: row;
}

@media (max-width: 1190px) {
  .header__top-left {
    flex-direction: column;
  }
}

@media (max-width: 540px) {
  .header__top-left {
    display: none;
  }
}

.header__top-logo {
  width: 215px;
  margin: 0;
  height: 100%;
  align-items: center;
}

.header__top-logo img {
  margin-left: -16px;
}

.header__top-rigth {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
}

@media (max-width: 1190px) {
  .header__top-rigth {
    flex-direction: column;
    gap: 10px;
  }
}

@media (max-width: 768px) {
  .header__top-rigth {
    width: 100%;
    gap: 0;
    flex-direction: row;
  }
}

.header__nav {
  font-family: 'romul', sans-serif;
}

.header-phone {
  padding: 0;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  display: flex;
  flex-wrap: nowrap;
  margin-right: 60px;
  line-height: 1.4;
  flex-direction: column;
  align-items: flex-start;
}

@media (max-width: 768px) {
  .header-phone {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.header-phone span {
  text-transform: uppercase;
  color: #d0aa8c;
  font-size: 11px;
  position: relative;
}

@media (max-width: 768px) {
  .header-phone span {
    font-size: 9px;
  }
}

.header-phone span::before {
  content: '';
  position: absolute;
  width: 40px;
  height: 1px;
  top: 50%;
  right: -50px;
  background-color: #d0aa8c;
}

.header .nav-bar {
  width: 100%;
  background-color: #2f2c34;
}

.header .btn {
  font-weight: 500;
  text-transform: uppercase;
  font-size: 13px;
  padding: 8px 35px;
  border: 1px solid black;
  margin-left: 25px;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

@media (max-width: 768px) {
  .header .btn {
    margin-left: 0;
  }
}

.header .btn:hover {
  border-color: #d0aa8c;
  background-color: #d0aa8c;
  color: #fff;
}

.header__nav {
  position: relative;
  margin: 0 15px;
  padding: 21px 15px;
  font-size: 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #2f2c34;
}

@media (max-width: 768px) {
  .header__nav {
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    width: 50%;
    position: fixed;
    top: 0;
    height: 100vh;
    z-index: 900;
    padding-left: 40px;
  }
}

.header__nav :first-child {
  margin-left: 0;
}

.header__nav :last-child {
  padding: 0;
  border-right: none;
}

.header__nav-item {
  color: white;
  border-right: 1px solid #fff;
  padding: 0 70px 0 0;
}

@media (max-width: 1190px) {
  .header__nav-item {
    padding-right: 30px;
  }
}

@media (max-width: 768px) {
  .header__nav-item {
    border: none;
  }
}

.header__nav-item a {
  width: 100%;
  transition: all .3s ease;
  color: #fff;
  position: relative;
}

.header__nav-item a:hover {
  color: #d0aa8c;
}

.header .menu_burger {
  width: 100px;
  height: 100%;
  display: none;
  color: white;
  font-size: 38px;
  z-index: 100;
  text-align: right;
  padding: 0 20px;
}

@media (max-width: 768px) {
  .header .menu_burger {
    display: block;
    margin: 0 20px 0 auto;
  }
}

.header .d-block {
  display: flex;
}

.footer {
  background-blend-mode: multiply;
  background: url("../img/footer.jpg") no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
  margin-top: 50px;
  position: relative;
  background-color: rgba(19, 19, 19, 0.9);
}

.footer__menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 35px 10px;
}

.footer__menu > div:nth-child(3) {
  border-right: 1px solid white;
}

@media (max-width: 1190px) {
  .footer__menu > div:nth-child(3) {
    border-right: 1px transparent white;
    text-align: right;
  }
  .footer__menu > div:nth-child(3) .nav-title::after {
    left: auto;
    right: 0;
  }
  .footer__menu > div:nth-child(3) .nav__items {
    border-left: 1px solid white;
  }
}

@media (max-width: 1190px) {
  .footer__menu > div:nth-child(2) {
    text-align: center;
  }
  .footer__menu > div:nth-child(2) .nav-title::after {
    right: 0;
    width: 100%;
    left: 0;
  }
  .footer__menu > div:nth-child(2) .nav__items {
    border-right: 1px transparent white;
  }
}

.footer__menu > div:nth-child(3) > ul {
  border-right: 1px transparent white;
}

.footer__menu .menu__nav {
  margin-right: 25px;
}

@media (max-width: 1190px) {
  .footer__menu .menu__nav {
    margin-right: 0;
  }
}

@media (max-width: 768px) {
  .footer__menu .menu__nav {
    display: none;
  }
}

.footer__menu .menu__nav-s {
  display: flex;
  flex-direction: column;
  margin: 0;
  text-align: center;
}

@media (max-width: 1190px) {
  .footer__menu .menu__nav-s {
    margin-top: 30px;
    width: 100%;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-end;
  }
}

@media (max-width: 768px) {
  .footer__menu .menu__nav-s {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}

.footer__menu .menu__nav-s .footer_btn {
  font-size: 17px;
  text-transform: uppercase;
  color: white;
  opacity: 0.6;
  border-bottom: 2px solid white;
  padding: 5px 20px;
  margin: 0 0 10px 0;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.footer__menu .menu__nav-s .footer_btn:hover {
  opacity: 1;
}

.footer__menu .menu__nav-s .social {
  background-color: #363636;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 15px;
}

@media (max-width: 768px) {
  .footer__menu .menu__nav-s .social {
    width: 100%;
    justify-content: space-around;
  }
}

.footer__menu .menu__nav-s .social-phone {
  margin-bottom: 5px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 17px;
}

@media (max-width: 768px) {
  .footer__menu .menu__nav-s .social-phone {
    align-items: center;
    text-align: left;
    margin: 0 0 14px 0;
  }
}

.footer__menu .menu__nav-s .social-phone span {
  font-size: 8px;
  color: #9a9a9a;
}

@media (max-width: 768px) {
  .footer__menu .menu__nav-s .social-phone span {
    font-size: 8px;
  }
}

.footer__menu .menu__nav-s .social-link {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .footer__menu .menu__nav-s .social-link {
    justify-content: space-evenly;
  }
}

.footer__menu .menu__nav-s .social-link a {
  font-size: 28px;
  color: #9a9a9a;
  transition: color 0.3s ease-in-out;
}

.footer__menu .menu__nav-s .social-link a:hover {
  color: #d0aa8d;
}

.footer__menu .nav-title {
  color: white;
  position: relative;
  font-size: 18px;
  font-family: 'romul', sans-serif;
  font-weight: 400;
  padding-bottom: 20px;
}

.footer__menu .nav-title::after {
  content: '';
  width: 70px;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 20;
  background-color: #d0aa8d;
}

.footer__menu .nav__items {
  width: 250px;
  margin-top: 30px;
  border-right: 1px solid white;
  margin-bottom: 0;
}

.footer__menu .nav__items-link {
  margin: 7px 0;
  font-size: 15px;
  font-family: 'Montsserat', sans-serif;
  font-weight: 500;
  color: white;
  opacity: 0.6;
  transition: opacity 0.3s ease-in-out;
  cursor: pointer;
}

.footer__menu .nav__items-link a {
  color: white;
}

.footer__menu .nav__items-link:hover {
  opacity: 1;
}

.footer_corp {
  height: 100%;
  color: white;
  font-size: 15px;
  opacity: .3;
  font-family: 'montserrat', sans-serif;
  font-weight: 500;
  padding: 30px 10px;
}

@media (max-width: 768px) {
  .footer_corp {
    font-size: 10px;
  }
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

@media (max-width: 1190px) {
  .wrapper {
    max-width: 900px;
  }
}

@media (max-width: 540px) {
  .wrapper {
    overflow: hidden;
  }
}

.bread-crumbs {
  width: 100%;
  border-bottom: 1px solid #2f2c343f;
  text-align: center;
  margin: 0;
  padding: 6px 0;
}

.bread-crumbs :first-child .bread-crumbs_item-link {
  border: none;
}

.bread-crumbs_item {
  display: inline;
  margin: 0;
}

.bread-crumbs_item-link {
  font-size: 11px;
  padding: 0 6px;
  border-left: 1px solid #2f2c34;
  text-transform: uppercase;
  color: #2f2c34;
}

.bread-crumbs_item-link-active {
  border-left: 1px solid #d0aa8c;
  color: #d0aa8c;
}

.section {
  text-align: center;
}

.section__title {
  text-transform: uppercase;
  font-family: 'romul', sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 30px 0 24px 0;
}

.section__title span {
  width: 100%;
  font-size: 17px;
  font-family: 'Montsserat', sans-serif;
  font-weight: 500;
  letter-spacing: 1px;
}

.section__title h2 {
  font-size: 38px;
  margin: 0;
  line-height: 1.3;
  position: relative;
  font-weight: 400;
}

.section__title h2::before {
  top: 50%;
  left: -90px;
  content: '';
  position: absolute;
  width: 80px;
  height: 2px;
  background-color: #d0aa8c;
}

.section__title h2::after {
  top: 50%;
  right: -90px;
  content: '';
  position: absolute;
  width: 80px;
  height: 2px;
  background-color: #d0aa8c;
}

.section__text {
  text-align: center;
  font-size: 13px;
  line-height: 1.34;
  width: 60%;
  margin: 0 auto;
}

@media (max-width: 1190px) {
  .section__text {
    width: 90%;
  }
}

.collection {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
}

@media (max-width: 1190px) {
  .collection {
    flex-direction: column;
    align-items: center;
  }
}

.collection .card {
  height: 100%;
  margin: 37px 15px 5px;
  max-width: 555px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 540px) {
  .collection .card {
    max-width: 100%;
    margin: 35px 0;
  }
}

.collection .card__img {
  height: 235px;
  overflow: hidden;
}

@media (max-width: 540px) {
  .collection .card__img {
    height: 100%;
    min-height: 200px;
  }
}

.collection .card__img img {
  width: 100%;
}

.collection .card__title {
  text-transform: uppercase;
  font-family: 'romul', sans-serif;
  font-weight: 400;
  font-size: 25px;
  margin: 20px 0;
  letter-spacing: 1px;
  line-height: 1.3;
  position: relative;
  align-self: center;
}

.collection .card__title::after {
  bottom: -20px;
  left: 140px;
  right: 0;
  content: '';
  position: absolute;
  width: 90px;
  height: 1px;
  background-color: #d0aa8c;
}

.collection .card__text {
  font-size: 13px;
  padding: 15px 0;
  margin: 0;
}

.collection .card__button {
  font-size: 15px;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  border: 2px solid #2f2c34;
  padding: 11px 84px;
  transition: all .3s ease-in-out;
  cursor: pointer;
}

.collection .card__button:hover {
  border-color: #d0aa8c;
  background-color: #d0aa8c;
  color: #fff;
}
