.footer {
  background-blend-mode: multiply;
  background: url('../img/footer.jpg') no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
  margin-top: 50px;
  position: relative;
  background-color: rgba($color: #131313, $alpha: 0.9);


  &__menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 35px 10px;

    >div:nth-child(3) {

      border-right: 1px solid white;

      @include lg {

        border-right: 1px transparent white;
        text-align: right;
        .nav-title{
          &::after{
            // left: -000010px;
            // right: -10000px;
            left: auto;
            right: 0;

          }



        }.nav__items {
          border-left: 1px solid white;
        }
      }
    }

    >div:nth-child(2) {
      @include lg {
        text-align: center;

        .nav-title{
          &::after{
            // left: 100px;
            right: 0;
            width: 100%;
            left: 0;
          }
        }

        .nav__items {
          border-right: 1px transparent white;
        }
      }
    }

    >div:nth-child(3)>ul {
      border-right: 1px transparent white;
    }

    .menu__nav {
      margin-right: 25px;

      @include lg {
        margin-right: 0;
      }

      @include md {
        display: none;
      }
    }

    .menu__nav-s {
      // margin-right: 80px;

      display: flex;
      flex-direction: column;
      // max-height: 100%;
      margin: 0;
      text-align: center;

      @include lg {
        margin-top: 30px;
        width: 100%;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: flex-end;
      }

      @include md {
        height: 100%;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

      }
      .footer_btn {
        font-size: 17px;
        text-transform: uppercase;
        color: white;
        opacity: 0.6;
        border-bottom: 2px solid white;
        padding: 5px 20px;
        margin: 0 0 10px 0;
        cursor: pointer;
        transition: all .3s ease-in-out;

        &:hover {
          opacity: 1;
        }
      }

      .social {
        background-color: #363636;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 10px 15px;

        @include md {
          width: 100%;
          justify-content: space-around;
        }
        &-phone {
          margin-bottom: 5px;
          color: white;
          display: flex;
          flex-direction: column;
          // justify-content: space-between;
          align-items: flex-end;
          font-size: 17px;

          @include md {
            align-items: center;
            text-align: left;
            margin: 0 0 14px 0;
          }
          span {

            font-size: 8px;
            color: #9a9a9a;
            @include md {
              font-size: 8px;
            }
          }
        }


        &-link {
          display: flex;
          justify-content: space-between;
          @include md {
            justify-content: space-evenly;
          }
          a {
            font-size: 28px;

            color: #9a9a9a;
            transition: color 0.3s ease-in-out;

            &:hover {
              color: #d0aa8d;
            }
          }
        }
      }

    }

    .nav {

      &-title {
        color: white;
        position: relative;
        font-size: 18px;
        font-family: 'romul', sans-serif;
        font-weight: 400;
        padding-bottom: 20px;

        &::after {
          content: '';
          width: 70px;
          height: 2px;
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: 20;
          background-color: #d0aa8d;
        }
      }

      &__items {
        width: 250px;
        margin-top: 30px;
        border-right: 1px solid white;
        margin-bottom: 0;

        &-link {
          margin: 7px 0;
          font-size: 15px;
          font-family: 'Montsserat', sans-serif;
          font-weight: 500;
          color: white;
          opacity: 0.6;
          transition: opacity 0.3s ease-in-out;
          cursor: pointer;

          a {
            color: white;
          }

          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }

  &_corp {
    height: 100%;
    color: white;
    font-size: 15px;
    opacity: .3;
    font-family: 'montserrat', sans-serif;
    font-weight: 500;
    padding: 30px 10px;

    @include md {
      font-size: 10px;
    }
  }
}
