@import "vars";
@import "mixins";
@import "fonts";
@import "vendor";
@import "settings";
@import "_header.scss";
@import "_footer";
@import "_iconsfont.css";

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  // background-color: rgba(0, 0, 0, 0.11);
  @include lg {
    max-width: 900px;
  }
  @include sm {
    overflow: hidden;
  }
}

.bread-crumbs {
  width: 100%;
  border-bottom: 1px solid #2f2c343f;
  text-align: center;
  margin: 0;
  padding: 6px 0;

  :first-child .bread-crumbs_item-link {
    border: none;
  }

  &_item {
    display: inline;
    margin: 0;


    &-link {
      font-size: 11px;
      padding: 0 6px;
      border-left: 1px solid #2f2c34;
      text-transform: uppercase;
      color: #2f2c34;
    }

    &-link-active {
      border-left: 1px solid #d0aa8c;
      color: #d0aa8c;
    }
  }
}


.section {
  text-align: center;

  &__title {
    text-transform: uppercase;
    font-family: 'romul', sans-serif;
    font-weight: 400;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 30px 0 24px 0;

    span {
      width: 100%;
      font-size: 17px;
      font-family: 'Montsserat', sans-serif;
      font-weight: 500;
      letter-spacing: 1px;
    }

    h2 {
      font-size: 38px;
      margin: 0;
      line-height: 1.3;
      position: relative;
      font-weight: 400;

      &::before {
        top: 50%;
        left: -90px;
        content: '';
        position: absolute;
        width: 80px;
        height: 2px;
        background-color: #d0aa8c;
      }

      &::after {
        top: 50%;
        right: -90px;
        content: '';
        position: absolute;
        width: 80px;
        height: 2px;
        background-color: #d0aa8c;
      }
    }
  }

  &__text {
    text-align: center;
    font-size: 13px;
    line-height: 1.34;
    width: 60%;
    margin: 0 auto;

    @include lg {
      width: 90%;
    }


  }
}




.collection {

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
  @include lg {
    flex-direction: column;
    align-items: center;
  }
  .card {
    height: 100%;
    margin: 37px 15px 5px;
    max-width: 555px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include sm {
      max-width: 100%;
      margin: 35px 0;
    }

    &__img {
      height: 235px;
      overflow: hidden;
      @include sm {
        height: 100%;
       min-height: 200px;
      }
      img {
        width: 100%;
      }
    }

    &__title {
      text-transform: uppercase;
      font-family: 'romul', sans-serif;
      font-weight: 400;
      font-size: 25px;
      margin: 20px 0;
      letter-spacing: 1px;
      line-height: 1.3;
      position: relative;
      align-self: center;

      &::after {
        // top: 50%;
        bottom: -20px;
        left: 140px;
        right: 0;
        content: '';
        position: absolute;
        width: 90px;
        height: 1px;
        background-color: #d0aa8c;
      }
    }

    &__text {
      font-size: 13px;
      padding: 15px 0;
      margin: 0;
    }

    &__button {
      font-size: 15px;
      text-transform: uppercase;
      font-family: 'Montserrat', sans-serif;
      border: 2px solid #2f2c34;
      padding: 11px 84px;
      transition: all .3s ease-in-out;
      cursor: pointer;

      &:hover {
        border-color: #d0aa8c;
        background-color: #d0aa8c;
        color: #fff;
      }

    }
  }


}

