@font-face {
  font-family: 'ico';

  src:  url('../iconfonts/ico.eot?rr08q2');
  src:  url('../iconfonts/ico.eot?rr08q2#iefix') format('embedded-opentype'),
    url('../iconfonts/ico.ttf?rr08q2') format('truetype'),
    url('../iconfonts/ico.woff?rr08q2') format('woff'),
    url('../iconfonts/ico.svg?rr08q2#ico') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="_s-"], [class*=" _s-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'ico' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

._s-menu:before {
  content: "\e904";
}
._s-google-plus:before {
